import { createWebHistory, createRouter } from "vue-router";
import Home from './components/CompoHome.vue';
import List from './components/CompoList.vue';
import Detail from './components/CompoDetail.vue';
import Error from './components/CompoError.vue';
import Author from './components/CompoAuthor.vue';
import Comment from './components/CompoComment.vue';




const routes = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/list",
    component: List,
    name: List,
  },
  {
    path: "/detail/:id",  /*(\\d+)숫자만 찾아주는 정규식 문법 , :작명*/
    component: Detail,
    name: Detail,
    children: [
    //특정 페이지에서 route를 나누고 싶을때 사용
      {
        path: "author", //상대경로로 적어줘야함 
        component: Author,
        name: Author,
      },
      {
        path: "comment",
        component: Comment,
        name: Comment,

      },
    ]
  },
  {
    // 404 페이지 만들기
    // 모든 문자가 들어오면 이라는 정규표현식 (.*)
    path: "/:anything(.*)",
    component: Error,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 