<template>
  <div> <!-- 최상단 div -->

    <div v-for="(blogData, idx) in blogData" :key="idx">
      <!-- $router는 페이지 이동관련 기능 -->
      <h5 @click="$router.push('/detail/' + idx)">{{ blogData.title }}</h5>
      
      <!-- path로 연결하는 법  -->
      <!-- <router-link :to="`/detail/${idx}`">{{ blogData.title }}</router-link> -->
      
      <p>{{ blogData.content }}</p>
    </div>

  </div> <!-- //최상단 div -->
</template>

<script>
export default {
  name: 'CompoList',
  props: {
    blogData : Array,
  },
}
</script>

<style>
h5{
  cursor: pointer;
}
</style>